.mainContainer010 {
  background-color: #D1E9F6;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
 
.login-page010 {
  background-color: #D1E9F6;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
 
.container010 {
  max-width: 400px;
  background: #F8F9FD;
  background: linear-gradient(0deg, rgb(255, 255, 255) 0%, rgb(244, 247, 251) 100%);
  border-radius: 40px;
  padding: 25px 35px;
  border: 5px solid rgb(255, 255, 255);
  box-shadow: rgba(133, 189, 215, 0.8784313725) 0px 30px 30px -20px;
  margin: 20px auto;
}
 
.heading010 {
  text-align: center;
  font-weight: 900;
  font-size: 30px;
  color: rgb(16, 137, 211);
}
 
.form010 {
  margin-top: 20px;
}
 
.form010 .input010 {
  width: 90%;
  background: white;
  border: none;
  padding: 15px 20px;
  border-radius: 20px;
  margin-top: 15px;
  box-shadow: #cff0ff 0px 10px 10px -5px;
  border-inline: 2px solid transparent;
}
 
.form010 .input010::-moz-placeholder {
  color: rgb(170, 170, 170);
}
 
.form010 .input010::placeholder {
  color: rgb(170, 170, 170);
}
 
.form010 .input010:focus {
  outline: none;
  border-inline: 2px solid #12B1D1;
}
 
.form010 .login-button010 {
  display: block;
  width: 30%;
  font-weight: bold;
  background: linear-gradient(45deg, rgb(16, 137, 211) 0%, rgb(18, 177, 209) 100%);
  color: white;
  padding-block: 15px;
  margin: 20px auto;
  border-radius: 20px;
  box-shadow: rgba(133, 189, 215, 0.8784313725) 0px 20px 10px -15px;
  border: none;
  transition: all 0.2s ease-in-out;
}
 
.form010 .login-button010:hover {
  transform: scale(1.03);
  box-shadow: rgba(133, 189, 215, 0.8784313725) 0px 23px 10px -20px;
}
 
.form010 .login-button010:active {
  transform: scale(0.95);
  box-shadow: rgba(133, 189, 215, 0.8784313725) 0px 15px 10px -10px;
}
 
.register-button010 {
  margin-left: 5px;
  text-align: center;
  color: #007bff;
  background: none;
  border: none;
  cursor: pointer;
  font-size: 15px;
  margin-top: 10px;
}
 
.password-container010 {
  position: relative;
}
 
.password-toggle010 {
  font-size: 0.9rem;
  position: absolute;
  right: 10px;
  top: 65%;
  transform: translateY(-50%);
  cursor: pointer;
  color: rgb(16, 137, 211);
}
 
 
@media (max-width: 768px) {
.container010 {
  max-width: 500px;
  padding: 15px 20px;
}
 
.heading010 {
  font-size: 26px;
}
 
.form010 .input010 {
  padding: 12px 15px;
}
 
.form010 .login-button010 {
  padding: 12px;
  font-size: 16px;
}
}
 
@media (max-width: 480px) {
.container010 {
  max-width: 280px;
  padding: 10px 15px;
}
 
.heading010 {
  font-size: 20px;
}
 
.form010 .input010 {
  padding: 10px;
}
 
.form010 .login-button010 {
  padding: 10px;
  font-size: 14px;
}




.spinner-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; /* Full screen height */
}

.spinner {
  border: 4px solid rgba(0, 0, 0, 0.1);
  border-left-color: #000;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

}
 
 